import React from 'react'
import PropTypes from 'prop-types'

const TableContents = ({ title = "Hello World", previous, next }) => {
    console.log(previous)
    console.log(next)
    return (
        <aside className="aside typeset aside--right">
            <section className="section section--description">
                <h3>{title}</h3>
            </section>
            <section className="">
                {previous && previous.node.fields.slug}{' '}{next && next.node.fields.slug}
            </section>
        </aside>
    )
}

TableContents.propTypes = {
    title: PropTypes.string,
    previous: PropTypes.shape({}).isRequired,
    next: PropTypes.shape({}).isRequired
}

export default TableContents